import React, { Component } from 'react';
import Slider from "react-slick";
import WoodsMap from '../data/woodsMap'
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

class Woods extends Component {
    state = {
        activeSlide: 0,
        activeSlide2: 0,
        headerImage: {}
    }
    render() {
        if (WoodsMap[sessionStorage.getItem("make")] &&
            WoodsMap[sessionStorage.getItem("make")][sessionStorage.getItem("year")]) {
            const woodsMap = WoodsMap[sessionStorage.getItem("make")][sessionStorage.getItem("year")]
            var settings = {
                dots: true,
                beforeChange: (current, next) => {    
                    const wood = woodsMap[next]
                    sessionStorage.setItem('wood', Object.keys(wood))
                    this.setState({headerImage: wood[Object.keys(wood)[0]]}, () => {
                        this.props.handlerToUpdate(this.state.headerImage)
                    })                
                    this.setState({ activeSlide: next })
                },
                afterChange: current => this.setState({ activeSlide2: current })
            }
            const woods = woodsMap.map((wood, idx) => {                
                return (
                    <div key={idx} >                                               
                        <img src={wood[Object.keys(wood)]} />
                        {Object.keys(wood)} 
                    </div>
                )
            })
            return (
                <div className="container">            
                    <Slider {...settings}>   
                        {woods}                
                    </Slider>
                </div>
            );
        }
        else {
            return (<div/>)
        }
    }
}

export default Woods