import React, { Component } from 'react';
import Slider from "react-slick";
import FloorplansMap from '../data/floorplansMap'

import MakesModels from '../data/makesModels.json';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

const styles = theme => ({
    root: {
        ...theme.mixins.gutters(),
        padding: theme.spacing.unit * 2,
    },
});

class Floorplans extends Component {    
    state = {
        activeSlide: 0,
        activeSlide2: 0,
        headerImage: {}
    }
    render() { 
        const {dieselOrGas, make, year, model} = this.props
        if (MakesModels[dieselOrGas] &&
            MakesModels[dieselOrGas][make] &&
            MakesModels[dieselOrGas][make][year] &&
            MakesModels[dieselOrGas][make][year][model]) {
            const floorplans = (MakesModels[dieselOrGas][make][year][model]).map((floorplan) => {
                return (floorplan) 
            })
            const floorplansDiv = floorplans.map((floorplan, idx) => {   
                return (    
                    <div key={idx} >                                           
                        <img src={FloorplansMap[year + ' ' + model + ' ' + floorplan]} />
                        {floorplan} 
                    </div>
                );
            }) 
            var settings = {
            dots: true,
            beforeChange: (current, next) => {  
                this.setState({headerImage: FloorplansMap[year + ' ' + model + ' ' + floorplans[next]]}, () => {                        
                    this.props.handlerToUpdate(this.state.headerImage)
                })                
                this.setState({ activeSlide: next })
            },
            afterChange: current => this.setState({ activeSlide2: current })
            }             
            return (                
                <div className="container">            
                    <Slider {...settings}>   
                        {floorplansDiv}                
                    </Slider>
                </div>
            );
        }        
        else {
            return (<div/>)
        
        }
    }
}

export default Floorplans